
import { type NextRequest, NextResponse } from 'next/server';
import * as admin from 'firebase-admin';

// Initialize the Firebase Admin SDK
// This should only happen once per server instance.
if (!admin.apps.length) {
  try {
    // This will use the GOOGLE_APPLICATION_CREDENTIALS environment variable
    // automatically provided in the App Hosting environment.
    admin.initializeApp();
  } catch (error) {
    console.error('Firebase Admin SDK initialization error:', error);
  }
}

export async function POST(req: NextRequest) {
  try {
    const { uid, role, churchId } = await req.json();

    if (!uid || !role || !churchId) {
      return NextResponse.json({ error: 'Missing uid, role, or churchId' }, { status: 400 });
    }

    const claims = {
      admin: role === 'admin',
      churchId: churchId,
    };

    await admin.auth().setCustomUserClaims(uid, claims);
    
    // It's good practice to also update the user record to allow for claim revocation tracking if needed.
    // This is optional but can be useful for security.
    await admin.firestore().collection('users').doc(uid).set({
      claimsLastUpdated: admin.firestore.FieldValue.serverTimestamp()
    }, { merge: true });

    return NextResponse.json({ success: true, message: `Claims set for user ${uid}` });
  } catch (error: any) {
    console.error('Error setting custom claims:', error);
    return NextResponse.json({ error: 'Internal Server Error', details: error.message }, { status: 500 });
  }
}
